import react from 'react';
import styled from 'styled-components';

/* Wrapper" */
const StyledOutWrapper = styled.div`
    @import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');
    font-family: 'Roboto', sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    background: #E5E5E5;
    }


`

/*// Frame //*/
const PhoneFrame = styled.div`

  
    color:white;
    background: #E0F1F2;
    transition: background 0.4s, color 0.4s;

    width: 411px;
    height: 731px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #0000000;
    flex-direction:"row-reverse";
    overflow:hidden;

    svg, button{cursor: pointer;}

    #full_screen{
      position: absolute;
      background: #00000057;
      z-index: 99999;
      width: 0%;
      height: 100%;
    }
    .popup{
      position: absolute;
      width: 280px;
      height: 174px;
      background-color: white;
      z-index: 999999;
      border-radius: 8px;
      color:black;
      text-align:center;
      display: none;
    }
    .popup_active{display: block !important;}
    .full_screenOn{width: 100% !important;}
`;

const AppBarTop = styled.div`

  
    background: #8155B4;

    width: 411px;
    height: 56px;
    position: absolute;
    top: 0px;
    left: 0px;

    svg{
        margin: 0;
        position: absolute;
        top: 50%;
        -ms-transform: translateY(-50%);
        transform: translateY(-50%);
        padding-left: 18px;

    }

`;


const StyledHeader = styled.h1`
font-family: 'Baloo Bhai 2', cursive;
text-align:center;
color: #8155B4;
font-size: 4.5rem;
margin: 30px 0px 0px 0px;


`;


const StyledHeader2 = styled.h4`
font-family: 'Baloo Bhai 2', cursive;
text-align:center;
margin: 0px 0px 0px 0px;
font-size: 1.5rem;
color: #8155B4;
padding-bottom:100px;

`;

const StyledHeader3 = styled.h1`
font-family: 'Baloo Bhai 2', cursive;
text-align:center;
display: flex;
flex-direction: column;
align-items: flex-start;
width: 200px;
margin: 25px 24px 24px 24px;
color: black;
font-size: 1rem;
font-weight:normal;
text-align: left;



`;


const StyledHeader4 = styled.h4`
font-family: 'Baloo Bhai 2', cursive;
text-align:center;
margin: 0px 0px 0px 0px;
font-size: 0.8rem;
color: #959595;
padding-bottom:100px;
font-weight:normal;

display: flex;
flex-direction: column;
align-items: flex-start;
width: 200px;
margin: 0px 0px 0px 24px;
padding: 0px 0px 20px 0px;;
text-align: left;
`;

const StyledButtonCancel = styled.button`

width: 84px;
height: 36px;
font-weight: bold;  
text-transform: uppercase;
font-size: .875rem;
font-family: 'Baloo Thambi 2', cursive;


color: #8155B4;
background: white;
border: none;




`;

const StyledButtonDownload = styled.button`

width: 84px;
height: 36px;
font-weight: bold;  
text-transform: uppercase;
font-size: .875rem;
font-family: 'Baloo Thambi 2', cursive;


color: #8155B4;
background: white;
border: none;
position: absolute;
right: 0;
margin-right: 8px;

`;



function popup_active(){
  document.getElementById("popup_active").classList.add("popup_active");
  document.getElementById("full_screen").classList.add("full_screenOn");
}
function popup_disactive(){
  document.getElementById("popup_active").classList.remove("popup_active");
  document.getElementById("full_screen").classList.remove("full_screenOn");
}

